import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';
import { FormBuilderService } from '@xenon/layouts';
import { ApiFormService, ApiRoleService, FormTemplate, Role } from '@xenon/api';
import { map, mergeMap, takeUntil } from 'rxjs/operators';

export interface FormSettings {
  formTemplateName: string;
  pluralName: string;
  description: string;
  formNames: string;
  roles: Role[];
  permissions: string[];
}

@Component({
  selector: 'xenon-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SettingsComponent implements OnInit, OnDestroy {
  formTemplateName = 'Form_Template_Name';
  formPluralName = 'Plural_Name';
  formTemplateDesc = 'Description';
  // selectedPermissions: string[];
  formNames: string;
  currentForm: FormTemplate;
  hasRoles = false;
  rolesList: Role[];
  permissionsList = ['Create', 'View', 'Edit', 'Delete'];

  unsubscribe = new Subject<{}>();

  constructor(private formBuilderService: FormBuilderService, private apiRoleService: ApiRoleService, private apiFormService: ApiFormService) {}

  ngOnInit(): void {
    this.getRoles();
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  getRoles() {
    this.formBuilderService.currentForm
      .pipe(
        takeUntil(this.unsubscribe),
        map((form) => {
          this.currentForm = form;
          const orgID = form.OrgID;
          return orgID;
        }),
        mergeMap((orgID) => this.apiRoleService.getRolesByOrg(orgID))
      )
      .subscribe((response) => {
        this.rolesList = response.OrgRoleList;
        if (this.rolesList && this.rolesList.length > 0) {
          this.hasRoles = true;
        }
      });
  }

  compareFn(option, value): boolean {
    return option && value ? option.ID === value.ID : option === value;
  }
}
